import React, { useCallback, useMemo, useState } from 'react';
import {
  Box, Button, FormControl, FormLabel, Input, Select
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { wirePay } from '../../../store/middlewares/payment';
import { updateContact } from '../../../store/middlewares/contact';
import { apiErrorHandler } from '../../../utils/general';
import CurrencyInput from '../../../components/CurrencyInput';
import AccountNumberInput from '../../../components/AccountNumberInput';
import RoutingNumberInput from '../../../components/RoutingNumberInput';
import { useDrawer } from '../../../hooks/drawer';

const accountTypes = {
  personalSavings: 'Personal Savings',
  personalChecking: 'Personal Checking',
  businessSavings: 'Business Savings',
  businessChecking: 'Business Checking'
};

const PaymentSuccess = React.lazy(() => import('../PaymentSuccess/PaymentSuccess'));

const DomesticWirePayment:React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const { open } = useDrawer();

  const accountDetails = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const contactDetails = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const [localContactDetails, updateContactData] = useState({
    address: contactDetails?.wire?.domestic?.address,
    accountNumber: contactDetails?.wire?.domestic?.accountNumber || '',
    routingNumber: contactDetails?.wire?.domestic?.routingNumber || '',
    bankName: contactDetails?.wire?.domestic?.bankName || '',
    accountType: contactDetails?.wire?.domestic?.accountType || 'personalSavings'
  });

  const [paymentData, updatePaymentData] = useState<{
    accountId: string,
    contactId: string,
    amount: string,
    description: string
    type: 'domestic'
  }>({
    amount: '',
    description: '',
    contactId: contactDetails?.id || '',
    accountId: accountDetails?.id || '',
    type: 'domestic'
  });

  const isValid = useMemo(() => localContactDetails.accountNumber.length !== 0 && localContactDetails.accountType.length !== 0 && localContactDetails.routingNumber.length !== 0 && localContactDetails.bankName.length !== 0 && paymentData.amount.length !== 0 && parseFloat(paymentData.amount) >= 0.01 && paymentData.description.length !== 0, [localContactDetails, paymentData]);

  const onSubmit = useCallback(() => {
    if (contactDetails?.id && isValid) {
      dispatch(updateContact(contactDetails.id, {
        accountId: contactDetails.accountId,
        wire: {
          domestic: {
            address: localContactDetails.address,
            accountNumber: localContactDetails.accountNumber,
            routingNumber: localContactDetails.routingNumber,
            bankName: localContactDetails.bankName,
            accountType: localContactDetails.accountType
          }
        }
      })).then(() => {
        dispatch(wirePay(paymentData))
          .then(() => {
            open(PaymentSuccess);
          }).catch(apiErrorHandler);
      }).catch(apiErrorHandler);
    }
  }, [isValid, paymentData, localContactDetails]);

  return (
    <div className="page">
      <MiniBox
        heading={t('payments_domestic_wire.title')}
        footer={(
          <Button width="100%" variant="primary" disabled={!isValid} onClick={onSubmit}>
            {t('payments_domestic_wire.ctaText')}
          </Button>
)}
      >
        <Box width="100%" paddingTop="15px">

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_domestic_wire.accountNumber')}
            </FormLabel>
            <AccountNumberInput
              value={localContactDetails.accountNumber}
              onChange={(_, v) => {
                updateContactData({
                  ...localContactDetails,
                  accountNumber: v
                });
              }}
            />
            {/* <Input
              variant="primary"
              maxLength={16}
              value={localContactDetails.accountNumber}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  accountNumber: e.target.value

                });
              }}
            /> */}
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_domestic_wire.routingNumber')}
            </FormLabel>
            <RoutingNumberInput
              value={localContactDetails.routingNumber}
              onChange={(_, v) => {
                updateContactData({
                  ...localContactDetails,
                  routingNumber: v

                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_domestic_wire.accountType')}
            </FormLabel>
            <Select
              variant="primary"
              value={localContactDetails.accountType}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  accountType: e.target.value

                });
              }}
            >
              {Object.keys(accountTypes).map((a) => <option key={a} value={a}>{(accountTypes as any)[a]}</option>)}

            </Select>
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_domestic_wire.bank')}
            </FormLabel>
            <Input
              variant="primary"
              value={localContactDetails.bankName}
              onChange={(e) => {
                updateContactData({
                  ...localContactDetails,
                  bankName: e.target.value

                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_domestic_wire.amount')}
            </FormLabel>
            <CurrencyInput
              value={paymentData.amount}
              onChange={(value) => {
                updatePaymentData({
                  ...paymentData,
                  amount: value || ''
                });
              }}
            />
          </FormControl>

          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('payments_domestic_wire.purpose')}
            </FormLabel>
            <Input
              variant="primary"
              value={paymentData.description}
              onChange={(e) => {
                updatePaymentData({
                  ...paymentData,
                  description: e.target.value
                });
              }}
            />
          </FormControl>
        </Box>
      </MiniBox>
    </div>
  );
};

export default DomesticWirePayment;
